import { StatusBadge } from "@/src/components/ui/status-badge"

// ... (Rest of imports)

// ...

export function TaskCalendar({ tasks, onAddClick, onEditClick, currentMonth, onMonthChange }: TaskCalendarProps) {
  // ... (State and helper functions same as before)
  
  const getStatusVariant = (status: string) => {
    switch (status) {
      case "completed": return "success"
      case "canceled": return "destructive"
      default: return "warning" // pending
    }
  }

  return (
    <>
      <Card className="bg-white shadow-sm flex flex-col h-full">
        <CardHeader className="flex flex-row items-center justify-between pb-2">
          <CardTitle className="font-heading text-lg font-semibold text-[#0A2540]">
            {monthNames[month]} {year}
          </CardTitle>
          <div className="flex items-center gap-2">
            <Button
              variant="outline"
              size="icon"
              onClick={prevMonth}
              className="h-8 w-8 border-[#0A2540] text-[#0A2540] hover:bg-[#0A2540] hover:text-white bg-transparent"
            >
              <ChevronLeft className="h-4 w-4" />
            </Button>
            <Button
              variant="outline"
              size="icon"
              onClick={nextMonth}
              className="h-8 w-8 border-[#0A2540] text-[#0A2540] hover:bg-[#0A2540] hover:text-white bg-transparent"
            >
              <ChevronRight className="h-4 w-4" />
            </Button>
            <Button onClick={onAddClick} className="ml-2 bg-[#0A2540] hover:bg-[#0A2540]/90 h-8 text-xs">
                <Plus className="h-3 w-3 mr-1" /> New
            </Button>
          </div>
        </CardHeader>
        <CardContent className="p-2 md:p-4 flex-1">
          <div className="grid grid-cols-7 gap-1 h-full auto-rows-fr">
            {dayNames.map((day) => (
              <div key={day} className="py-1 md:py-2 text-center text-xs md:text-sm font-medium text-muted-foreground">
                <span className="hidden sm:inline">{day}</span>
                <span className="sm:hidden">{day.charAt(0)}</span>
              </div>
            ))}
            
            {blanks.map((_, i) => (
                 <div key={`blank-${i}`} className="border-transparent" />
            ))}

            {daysInMonth.map((date, index) => {
              const dayTasks = getTasksForDate(date)
              const isToday = isSameDay(date, new Date())

              return (
                <div
                  key={index}
                  onClick={() => setSelectedDate(date)}
                  className={cn(
                    "min-h-[80px] rounded-lg border p-1 transition-all duration-200 ease-out cursor-pointer hover:border-[#00C49A]/30 hover:bg-[#F6F9FC] flex flex-col",
                    isToday ? "bg-[#00C49A]/5 border-[#00C49A]" : "border-border"
                  )}
                >
                  <div className={cn("text-xs md:text-sm font-medium mb-1", isToday ? "text-[#00C49A]" : "text-[#333]")}>
                     {date.getDate()}
                  </div>
                  
                  <div className="space-y-0.5 md:space-y-1 overflow-hidden">
                     {dayTasks.slice(0, 2).map(task => (
                         <div 
                            key={task.id} 
                            className="w-full truncate rounded bg-[#00C49A]/10 px-1 py-0.5 text-left text-[8px] md:text-xs text-[#00C49A]"
                         >
                             {task.title}
                         </div>
                     ))}
                     {dayTasks.length > 2 && (
                          <div className="text-[8px] md:text-xs text-muted-foreground">+{dayTasks.length - 2}</div>
                     )}
                  </div>
                </div>
              )
            })}
          </div>
        </CardContent>
      </Card>

      <Dialog open={!!selectedDate} onOpenChange={() => setSelectedDate(null)}>
        <DialogContent className="max-w-lg max-h-[90vh] overflow-hidden flex flex-col">
          <DialogHeader>
            <DialogTitle className="font-heading text-[#0A2540]">
                 Tasks for {selectedDate ? format(selectedDate, "PPP") : ""}
            </DialogTitle>
             <DialogDescription>
                 {selectedDate ? `${getTasksForDate(selectedDate).length} tasks scheduled` : ""}
             </DialogDescription>
          </DialogHeader>
          <div className="space-y-3 overflow-y-auto flex-1 pr-2">
             {selectedDate && getTasksForDate(selectedDate).length > 0 ? (
                getTasksForDate(selectedDate).map(task => (
                 <div 
                    key={task.id} 
                    className="cursor-pointer rounded-lg border border-border p-4 transition-all duration-200 hover:border-[#00C49A]/30 hover:bg-[#F6F9FC]"
                    onClick={() => { setSelectedDate(null); onEditClick(task); }}
                 >
                     <div className="flex items-start justify-between gap-2">
                         <div className="flex-1 min-w-0">
                             <p className="font-medium text-[#0A2540]">{task.title}</p>
                             <p className="text-sm text-muted-foreground mt-1">{task.jobName}</p>
                             {task.description && <p className="text-sm text-muted-foreground mt-1 line-clamp-2">{task.description}</p>}
                         </div>
                         <StatusBadge status={task.status} variant={getStatusVariant(task.status)} />
                     </div>
                     <div className="flex items-center justify-between mt-3 pt-3 border-t border-border">
                         <span className="text-sm">
                             <span className="text-muted-foreground">Technician: </span>
                             <span className="font-medium text-[#0A2540]">{task.technicianName || "Unassigned"}</span>
                         </span>
                         <span className="text-xs text-muted-foreground">
                             {format(new Date(task.taskDate), "p")}
                         </span>
                     </div>
                 </div>
                ))
             ) : (
                 <div className="text-center py-8">
                     <p className="text-muted-foreground">No tasks for this day.</p>
                     <Button variant="link" onClick={() => { 
                         setSelectedDate(null); 
                         onAddClick(); 
                     }}>
                         Create one?
                     </Button>
                 </div>
             )}
          </div>
        </DialogContent>
      </Dialog>
    </>
  )
}
